@import '../config';

.banner {
	.banner__box {
		background-color: transparent;
	}

	.banner__content {
		padding-top: 0;
		justify-content: flex-start;
	}

	.banner__heading {
		text-align: left;
		color: $white;
		font-size: 10.7rem;
		line-height: 11.25rem;
		font-weight: bold;
	}

	.banner__text {
		text-align: left;
		font-size: 1.7rem;
		line-height: 2.7rem;
	}
}